import React, { useRef, useEffect, useState } from 'react';
import subFlowchartData from '../../data/subFlowchartData';
import { initializeLowerDiagram } from './initializeLowerDiagram';
import { updateLowerDiagramData } from './updateLowerDiagramData';
import NodeModal from './NodeModal';

const LowerFlowchart = ({ selectedUpperNodeKey }) => {
  const diagramRef = useRef(null);
  const diagramInstance = useRef(null);
  const [selectedNode, setSelectedNode] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [buttonKey, setButtonKey] = useState(0); // para acionar a animao

  console.log(" LowerFlowchart renderizou! N selecionado (upper):", selectedUpperNodeKey);

  useEffect(() => {
    if (diagramInstance.current) {
      console.log(" LowerFlowchart: Diagrama j inicializado.");
      return;
    }

    // Inicializa o diagrama da lower chart
    diagramInstance.current = initializeLowerDiagram(diagramRef, (nodeData) => {
      console.log(" N da lower chart clicado:", nodeData);
      setSelectedNode(nodeData);
      // Atualiza o key para acionar a animao do boto
      setButtonKey(Date.now());
    });
  }, []);

  useEffect(() => {
    if (!diagramInstance.current) {
      console.log(" LowerFlowchart: Tentativa de atualizao antes da inicializao.");
      return;
    }

    console.log(" Buscando dados para a chave selecionada:", selectedUpperNodeKey);

    if (!selectedUpperNodeKey) {
      console.log(" Nenhuma chave selecionada. O diagrama ser limpo.");
      updateLowerDiagramData(diagramInstance.current, { nodeDataArray: [], linkDataArray: [] });
      // No alteramos selectedNode aqui
      return;
    }

    const data = subFlowchartData[selectedUpperNodeKey];

    if (!data) {
      console.log(` ERRO: Nenhum dado encontrado para a chave '${selectedUpperNodeKey}'. Verifique 'subFlowchartData.js'.`);
      return;
    }

    console.log(" Dados encontrados para o n selecionado:", data);
    updateLowerDiagramData(diagramInstance.current, data);
    // NO seleciona automaticamente nenhum n  espera a ao do usurio.
  }, [selectedUpperNodeKey]);

  const handleButtonClick = () => {
    console.log(" LowerFlowchart: Boto 'Contedo' pressionado.");
    setIsModalOpen(true);
  };

  return (
    <div className="flowchart-container">
      <div className="flowchart-diagram" ref={diagramRef}></div>
      {selectedNode && (
        // Usamos a propriedade key para forar uma re-montagem e disparar a animao
        <button key={buttonKey} className="content-button" onClick={handleButtonClick}>
          Contedo
        </button>
      )}
      <NodeModal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
          onModalClose={() => setSelectedNode(null)} // Reseta o n selecionado
          content={selectedNode ? selectedNode.text : "Nenhum n selecionado"}
        description={selectedNode ? selectedNode.description : "Sem detalhes disponveis."}
        nodeKey={selectedNode ? selectedNode.key : null}
        isUpperChart={false} // // Mudana: permitir exibir "Quem faz?" na Lower Chart tambm
      />
    </div>
  );
};

export default LowerFlowchart;
