import React from 'react';

const NodeButtons = ({ texts, isUpperChart, onEdit, onDisplay }) => {
    return (
        <>
            <button className="modal-button" onClick={() => onDisplay("De que se trata esse servio?", texts.whyImportant)}>
                De que se trata esse servio?
            </button>

            <button className="modal-button" onClick={() => onDisplay("Quem faz esse servio?", texts.whoDoes)}>
                Quem faz esse servio?
            </button>

            {!isUpperChart && ( //  "Como esse servio  feito?" s aparece na Lower Chart
                <button className="modal-button" onClick={() => onDisplay("Sugesto para testes", texts.howDoes)}>
                    Sugesto para Testes
                </button>
            )}

            <button className="modal-button edit" onClick={onEdit}>
                Gerenciar
            </button>
        </>
    );
};

export default NodeButtons;
